/* This will only apply to the landing page */
body.landing-page {
    background-color: #ffffff !important;
    background-image: none !important;
    color: #4686B5;
    display: flex;
    flex-direction: column;
    font-family: 'Fredoka One', 'Arial', sans-serif;
    -webkit-text-stroke: 0;
    text-shadow: none;
}

.landing-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    text-align: center;
    -webkit-text-stroke: 0;
    text-shadow: none;
    margin-top: 100px;
}

main {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 2rem;
    max-width: 1200px;
    width: 100%;
}

.hero {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    width: 100%;
}

.hero img {
    width: 400px;
    height: auto;
    margin-bottom: 2rem;
    animation: float 6s ease-in-out infinite;
}

.hero-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    text-align: center;
    width: 100%;
}

.tagline {
    font-size: 2.5rem;
    color: #4686B5;
    margin-bottom: 2.5rem;
    max-width: 600px;
    line-height: 1.6;
}

.start-button {
    background: #1D2D5E;
    color: #ffffff;
    border: none;
    padding: 1rem 2rem;
    font-size: 1.5rem;
    font-weight: bold;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(70, 134, 181, 0.3);
    margin: 0 auto;
    display: block;
    font-family: 'Fredoka One', 'Arial', sans-serif;
}

.start-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(70, 134, 181, 0.4);
    background: #3a6d94;
}

.start-button:active {
    transform: translateY(0);
}

@keyframes float {
    0% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-30px);
    }
    100% {
        transform: translateY(0px);
    }
}

@media (max-width: 768px) {
    .logo {
        font-size: 1.5rem;
    }

    .hero {
        flex-direction: column;
        gap: 2rem;
    }

    .hero img {
        width: 200px;
    }

    .tagline {
        font-size: 1.2rem;
    }

    .start-button {
        padding: 0.8rem 1.6rem;
        font-size: 1rem;
    }
}