.text-reader-controls {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
    gap: 20px;
    margin-top: 20px;
    margin-left: 20px;
}

.text-reader-btn {
    padding: 5px;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
}

.speaker-icon, .pause-icon, .resume-icon, .stop-icon {
    height: 50px;
    object-fit: contain;
}

.speaker-btn:hover, .pause-btn:hover, .resume-btn:hover, .stop-btn:hover {
    transform: scale(1.1);
    background: none;
}

.word {
    display: inline;
    padding: 0;
    margin: 0;
}

.word-highlight {
    background-color: rgba(255, 217, 0, 0.9);
    border-radius: 2px;
}
